import React, {Component} from 'react';
import Media from './media';
import Play from './iconos/presentacionales/play';
import Full from './iconos/presentacionales/fullscreem';
import Pausa from './iconos/presentacionales/pausa';
import Volumen from './iconos/presentacionales/volumen';

function Playlist(props) {
		return(
			<div className="playlist">
			
			{
				props.playlist.map((item)=>{
					return <Media handleClick={props.handleOpenModal}  {...item} key={item.id}/>
				})
			}
			</div>
			)
	}


export default Playlist;


